import { Button, Result, Spin, Modal } from "antd";
import React, {Component} from 'react';
import { login, initClient } from "./utils";
import './border.css'

class Online extends Component {
    state = {name: null, loading: true, loginLoading: false}

    async login() {
        const that = this
        that.setState({loginLoading: true})
        Modal.info({
            title: '',
            content: (
                <div>
                    
                </div>
            )
        })
        const code = await login()
        if (code === 0) {
            try {
                initClient().then(v=>{
                    if (v.td > 5) {
                        Modal.error({
                            title: '',
                            content: ''
                        })
                    } else {
                        Modal.destroyAll()
                        that.setState({name: v.name, loading: false, loginLoading: false})
                    }
                })
            } catch (_) {
                Modal.error({
                    title: '',
                    content: ''
                })
            }
        } else if (code === 11) {
            Modal.error({
                title: '',
                content: ''
            })
            that.setState({loginLoading: false})
        }
    }

    componentDidMount() {
        const that = this
        try {
            initClient().then(v=>{
                if (v.td > 5) {
                    Modal.error({
                        title: '',
                        content: ''
                    })
                } else {
                    that.setState({name: v.name, loading: false, loginLoading: false})
                }
            })
        } catch (_) {
            Modal.error({
                title: '',
                content: ''
            })
        }
    }

    render() {
        return (
            <div className="content">
                {
                    this.state.loading ?
                        <div style={{paddingTop: 150, textAlign: 'center'}}>
                            <Spin tip="..." size="large" />
                        </div> :
                        (
                            this.state.name ?
                                <div style={{paddingTop: 150, textAlign: 'center'}}>
                                    <h1>{this.state.name}</h1>
                                    <h2></h2>
                                </div> :
                                <Result
                                    status="info"
                                    title=""
                                    extra={
                                        <Button
                                            type="primary"
                                            size="large"
                                            loading={this.state.loginLoading}
                                            onClick={()=>this.login()}
                                        >
                                            
                                        </Button>
                                    }
                                />
                        )
                }
            </div>
        );
    }
}

export default Online;